.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
  background: rgb(249, 255, 243);
}

@font-face {
  font-family: 'metropolis';
  src: local('metropolis'), url(./assets/fonts/metropolis/Metropolis-BlackItalic.otf) format('opentype');
}
@font-face {
  font-family: 'syne';
  src: local('syne'), url(./assets/fonts/syne/Syne-Extra.otf) format('opentype');
}
@font-face {
  font-family: 'synebold';
  src: local('synebold'), url(./assets/fonts/syne/Syne-Bold.otf) format('opentype');
}

button:hover {
  background-color:#002ead;
}

@media screen and (min-width: 601px) {
  h5 {
    font-size: 1.5rem;
  }
}

/* If the screen size is 600px wide or less, set the font-size of <div> to 30px */
@media screen and (max-width: 600px) {
  h5 {
    font-size: 0.85rem;
  }
  h3 {
    font-size: 1.2rem;
  }
}